body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home__images {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.hero-text {
  text-align: center;
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

.hero-image1 {
  width: 100%;
  height: 750px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/back1.c3279765.jpg);
  background-size:  100% 100%;
  background-repeat: no-repeat;
  margin: 20px 0;
}

.hero-image2 {
  width: 100%;
  height: 750px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/back2.b0a7cf70.jpg);
  background-size: 100% 100% ;
  background-repeat: no-repeat;
  margin: 20px 0;
}
.hero-image3 {
  width: 100%;
  height: 750px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/back3.794168a5.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  margin: 20px 0;
}

@media screen and (max-width: 1100px) {
  .hero-body p {
    font-size: 16px;
  }

  .hero-text h1 {
    font-size: 20px;
  }

  .hero-text p {
    font-size: 12px;
  }

  .hero-image1 {
    height: 500px;
  }
  .hero-image2 {
    height: 500px;
  }
  .hero-image3 {
    height: 500px;
  }
}


@media screen and (max-width: 400px) {
  .hero-body p {
    font-size: 12px;
  }

  .hero-text h1 {
    font-size: 14px;
  }

  .hero-text p {
    font-size: 8px;
  }

  .hero-image1 {
    height: 300px;
  }
  .hero-image2 {
    height: 300px;
  }
  .hero-image3 {
    height: 300px;
  }
}

/* books component */
.books {
  margin-top: 100px;
}

.books__cards {
  width: 75%;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.book__card {
  width: 18rem;
  margin: 20px;
  cursor: pointer;
  transition: all 0.5s ease;
}

.book__card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.carousel__img {
  width: 150px;
  transition: all 0.5s ease;
  cursor: pointer;
}

.carousel__title p {
  font-size: 24px;
}

.carousel__img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.books__filters {
  width: 80%;
  margin: 30px auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.books__search {
  width: 50%;
  margin: auto;
}

.search_btn {
  font-size: 0.7rem !important;
}

.book__buttons {
  display: -webkit-flex;
  display: flex;
}

/* book component */
.book__component {
  width: 65%;
  margin: auto;
  margin-top: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  text-align: left;
}

.book__title__android {
  display: none;
}

.book__img {
  width: 40%;
  margin-top: 100px;
}

.book__info {
  width: 60%;
  padding: 20px;
  margin-left: 20px;
}

.favourite_btn,
.cart_btn {
  margin: 20px;
}

.comments__component {
  width: 65%;
  margin: auto;
  text-align: left;
}

.text__controller {
  cursor: pointer;
  margin: 0 10px;
}

.pagination {
  margin-top: 50px;
  -webkit-justify-content: center;
          justify-content: center;
}

@media screen and (max-width: 1100px) {
  /* books component */
  .book__component {
    width: 70%;
  }
  .favourite_btn,
  .cart_btn {
    font-size: 0.6rem !important;
  }
}

@media screen and (max-width: 850px) {
  .books__cards {
    width: 85%;
  }

  /* books component */
  .book__component {
    width: 80%;
  }

  .favourite_btn,
  .cart_btn {
    font-size: 0.6rem !important;
    padding: 10px 10px !important;
  }
}

@media screen and (max-width: 660px) {
  /* books component */
  .books {
    margin-top: 50px;
  }

  .carousel__title p {
    font-size: 16px;
  }

  .carousel__img {
    width: 100px;
  }

  .books__search {
    width: 80%;
    margin: auto;
  }

  .books__cards {
    width: 80%;
  }

  .book__buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }

  /* book component */
  .book__component {
    width: 90%;
    margin: auto;
    margin-top: 20px;
    display: block;
    text-align: center;
  }

  .book__title {
    display: none;
  }

  .book__title__android {
    display: block;
    font-size: 1.5rem;
    margin-bottom: 20px;
  }

  .book__img {
    width: 60%;
    margin-top: 20px;
    margin: auto;
  }

  .book__info {
    width: 90%;
    margin: 0 auto;
    padding: 0;
  }

  .book__info h3 {
    font-size: 1rem;
  }

  .book__info h5 {
    font-size: 0.9rem;
  }

  .book__info h6 {
    font-size: 0.75rem;
  }

  .book__info p {
    font-size: 0.6rem;
    text-align: left;
  }

  .book__info button {
    width: 100px;
    font-size: 8px;
    padding: 10px 5px;
    margin-bottom: 20px;
  }

  .book__author {
    margin-top: 20px;
  }

  .favourite_btn,
  .cart_btn {
    font-size: 0.4rem !important;
    padding: 8px 8px !important;

  }
}

.book__comments {
  width: 80%;
}

.bookComments {
  margin-top: 100px;
}

.comment__add {
  margin: 20px 0;
}

.book__comment {
  margin-bottom: 25px;
}

.comment__header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.comment__date {
  color: gray;
  margin: 0 10px;
}

.comment__button {
  float: right;
  margin: 10px 0;
}

.comment__showReplies {
  margin-top: 10px;
}

.comment__reply__input {
  width: 90%;
  margin: 10px 0;
}
.comment__reply_buttons button {
  width: 25%;
  padding: 5px;
}

.selectedLike {
  color: skyblue;
  margin: 0 10px;
  transition: all 0.5s ease;
  cursor: pointer;
}

.like {
  color: lightgray;
  margin: 0 10px;
  transition: all 0.5s ease;
  cursor: pointer;
}

.selectedLike:hover {
  color: gray;
  margin: 0 10px;
}

.like:hover {
  color: gray;
  margin: 0 10px;
}

/* comment__replies */
.comment__showReplies {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  cursor: pointer;
  color: blue;
}

.comment__showReplies p {
  margin-left: 5px;
}
.comment__replies {
  margin-left: 50px;
}

@media screen and (max-width: 660px) {
  .comments__component {
    width: 90%;
    margin: 0 auto;
    margin-top: 50px;
  }

  .comments__component h3 {
    font-size: 1rem;
  }

  .book__comments {
    width: 90%;
    margin: 0 auto;
  }

  .book__comments strong {
    font-size: 0.95rem;
  }

  .book__comments p {
    font-size: 0.85rem;
  }

  .comment__reply {
    width: 100%;
  }

  .comment__reply__p {
    white-space: pre;
  }

  .comment__reply__input {
    width: 100%;
  }
  .comment__reply_buttons button {
    width: 25%;
    padding: 5px;
  }

  .comment__add{
    width:90% !important;
  }

   .comment__button{
  font-size:0.55rem !important;
  padding: 5px 10px !important;
}
}

.ordered__books {
  margin-top: 100px !important;
  margin: auto;
  width: 60%;
}

.ordered__book {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 50px 0;
  padding: 20px;
  border: 1px solid crimson;
  box-shadow: 2px 3px 5px crimson;
  background-color: rgba(255, 217, 0, 0.055);
}

.ordered__book__title_android {
  display: none;
}

.ordered__book__cover {
  width: 30%;
  margin: 0 20px;
}
.ordered__book__info {
  width: 70%;
  text-align: left;
}

.ordered__book__title,
.ordered__book__author,
.ordered__book__genre {
  text-align: center;
  margin-bottom: 30px;    
}

.ordered__book__title{  
  font-weight:500;
}

.order__button {
  margin: 0 auto !important ;
  display: block !important;
}

@media screen and (max-width: 880px) {
  .ordered__book__title {
    display: none;
  }

  .ordered__book__title_android {
    display: block;
    font-size: 1.25rem;
    font-weight:500;
    margin-bottom: 20px;
  }

  .ordered__books {
    width: 85%;
    margin-top: 50px !important;
  }
  .ordered__book {
    display: block;
    text-align: center;
    padding: 10px;
  }

  .ordered__book__cover {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 20px;
  }

  .ordered__book__info {
    display: block;
    width: 90%;
    margin: 0 auto;
  }

  .ordered__book__info h5 {
    font-size: 1rem;
  }

  .ordered__book__info h6,
  strong {
    font-size: 0.9rem;
  }

  .ordered__book__info p {
    font-size: 0.75rem;
  }

  .ordered__book__info button {
    width: 50%;
    font-size: 0.5rem;
    padding: 5px;
    margin-bottom: 15px !important;
  }
}


@media screen and (max-width: 400px) {
  .ordered__book__info h5 {
    font-size: 0.85rem;
  }

  .ordered__book__info h6,
  strong {
    font-size: 0.75rem;
  }

  .ordered__book__info p {
    font-size: 0.6rem;
  }

}
.bookstore_navbar {
  position: fixed;
  top: 0;
  z-index: 999;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
  background-color: #f14668;
  padding-top: 10px;
}

.bookstore_navbar_android {
  display: none;
}

.first_nav {
  position: fixed;
  top: 0;
  z-index: 999;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  width: 100%;
  background-color: #f14668;
  padding-top: 10px;
}

.second_nav {
  margin-top: 66px;
  z-index: 999;
  width: 100%;
}

/* Navbar Search Form*/
.search__list {
  width: 100%;
  position: absolute;
  margin-top: 38px;
  background: white;
  color: black;
  text-align: left;
  font-size: 12px;
  box-shadow: black 3px 5px 10px;
  z-index: 999;
}
.search__list__element {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  border-bottom: 1px solid wheat;
  height: 60px;
  cursor: pointer;
  color: black;
}

.search__list__element:hover,
.search__all__element:hover {
  background: wheat;
}

.search__list__element img {
  width: 40px;
  margin: 0 10px;
}

.search__all__element {
  height: 60px;
  text-align: center;
  cursor: pointer;
}

.search__all__element p {
  padding-top: 20px;
}

.search_icon{  
background-color: gold;
color: crimson;
padding: 5px 10px;
padding-top: 10px;
cursor: pointer;
font-size:0.66rem !important;

}

@media screen and (max-width: 1100px) {
   .search__list__element strong {
    font-size: 0.6rem;
  }

   .search__list__element p {
    font-size: 0.5rem;
    margin: 5px 0 15px 0;
  }
}

@media screen and (max-width: 850px) {
  
  .navbar_brand, .navbar_item, .navbar-item{
    font-size:10px;
  }

  .navbar_brand, .navbar_item{
    width:8% !important;
  }
 
  .search_form{
    width: 30% !important;
    margin:0 !important;
  }

  .search__list {
    font-size: 10px;
  }

  .search__list__element p {
    font-size: 0.45rem;
    margin: 5px 0 15px 0;
  }

  .search__list__element strong {
    font-size: 0.6rem;
  }

  .search_icon{    
   font-size:0.66rem !important;
}

  .navbar_menu{
    width: 45% !important;
  }

  .github_btn{
    height:30px;
    width:90px;
    font-size:0.66rem;
  }
}



@media screen and (max-width: 660px) {
  .bookstore_navbar {
    display: none;
  }
  .bookstore_navbar_android {
    display: block;
  }

   .navbar_brand, .navbar_item{
    font-size:8px !important;
  }

  .search__list {
    font-size: 10px;
  }

  .search__list__element p {
    font-size: 0.5rem;
    margin: 5px 0 15px 0;
  }

  .search__list__element strong {
    font-size: 0.65rem;
  }

  .navbar_menu{
    width: 50% !important;
  }
}

